<template>
  <div class="row">
    <div class="col" v-on:click="onRestore">Restore</div>
    <div class="col" v-on:click="reset">Refresh</div>
  </div>
  <div class="hello">
    <img :src="imageUrl" alt="Image description" />
  </div>
  <div class="row">
    <div class="col" v-on:click="onAccept">Accept</div>
    <div class="col" v-on:click="onReject">Reject</div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "App",
  data() {
    return {
      fileName: "",
      imageUrl: "",
      baseUrl: "",
    };
  },
  created() {
    this.baseUrl = window.__baseUrl;
    this.getNotAcceptedImage();
  },
  methods: {
    async onRestore() {
      await axios.post(this.baseUrl + "/unverify-last-img");
      this.reset();
    },
    reset() {
      this.fileName = "";
      this.imageUrl = "";
      this.getNotAcceptedImage();
    },
    async onAccept() {
      await axios.post(this.baseUrl + "/accept-img/" + this.fileName);
      this.reset();
    },
    async onReject() {
      await axios.post(this.baseUrl + "/reject-img/" + this.fileName);
      this.reset();
    },
    async getNotAcceptedImage() {
      const res = await axios.post(this.baseUrl + "/get-first-image-to-verify");
      console.log(res);
      if (res.status == 200 && res.data.Data.length) {
        this.imageUrl =
          this.baseUrl + "/get-image/" + res.data.Data[0].file_name;

        this.fileName = res.data.Data[0].file_name;
      }
    },
  },
};
</script>

